import { ErrorTip } from '@engine/core';
import { AbstractErrorHandler, ErrorData } from './error_handler.abstract';

export class GlobalErrorHandler extends AbstractErrorHandler {
    public handle(data: ErrorData) {
        const id = BI.UUID();
        const layer = BI.Layers.create(id);
        BI.createWidget({
            type: BI.CenterAdaptLayout.xtype,
            cls: 'bi-card',
            element: layer,
            items: [{
                type: ErrorTip.xtype,
                ...data,
            }],
        });
        BI.Layers.show(id);
    }
}
